.label-info {
  display: inline-block;
  vertical-align: top;
  padding: 0 4px;
  border-radius: 4px;
  font-weight: 500;
  font-size: 10px;
  line-height: 16px;
  color: $colorYellowLight;
  background: #ccc;

  &--mcolor {
    background-color: $colorPink; }

  &--primary {
    background-color: $colorPrimary; }

  &--extra {
    background-color: $colorExtra; } }

.label-info + .label-info {
  margin-left: 4px; }
